.section {
  padding: 100px 0;
  color: #222;
  background-color: #fff;
  border-radius: 10px;
  margin: 0 20px;

  &-container {
    padding: 10px 0;

    &:first-of-type {
      background-image: linear-gradient(
        #fff 0%,
        #fff calc(100% - 10px),
        rgba(110, 153, 65, 0.6) calc(100% + 10px)
      );
    }

    &:nth-of-type(2n + 3) {
      background-image: linear-gradient(
        rgba(110, 153, 65, 0.6) -10px,
        #fff 10px,
        #fff calc(100% - 10px),
        rgba(110, 153, 65, 0.6) calc(100% + 10px)
      );
    }

    &:nth-of-type(2n) {
      background-image: linear-gradient(
        #fff -10px,
        rgba(110, 153, 65, 0.6) 10px,
        rgba(110, 153, 65, 0.6) calc(100% - 10px),
        #fff calc(100% + 10px)
      );

      .section {
        box-shadow: 0px 2px 10px -2px rgba(34, 34, 34, 0.3);
      }
    }

    &:last-of-type {
      background-image: linear-gradient(
        #fff -10px,
        rgba(110, 153, 65, 0.6) 10px
      );
    }
  }

  &__heading {
    text-align: center;
    margin-bottom: 100px;
  }
}

.navigation {
  $self: &;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 26px 0;
  z-index: 2;
  background-color: #fff;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.03), 0 2px 8px rgba(0, 0, 0, 0.12);

  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__logo {
    &-container {
      height: 80px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
    height: 80px;
    cursor: pointer;
  }
}

.contact-info {
  &__link {
    position: relative;
    font-weight: 500;
    font-size: 16px;
    color: #222;
    text-decoration: none;
    cursor: pointer;
    transition: color 0.25s ease;

    &-icon {
      height: 16px;
      margin-right: 10px;
    }

    &-container {
      &:not(:last-of-type) {
        margin-right: 30px;
      }
    }
  }
}

.header {
  position: relative;
  margin: 110px 20px 20px;
  padding: 100px 0;
  min-height: calc(100vh - 140px);
  box-shadow: 0px 1px 10px -2px rgba(34, 34, 34, 0.2);
  display: flex;
  align-items: center;
  background: url(./assets/img/header-bg.jpg) center / cover no-repeat;
  text-align: center;

  &,
  &__background {
    border-radius: 10px;
  }

  &__background {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    border-radius: 10px;
  }

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__text-container {
    max-width: 700px;
    margin: 0 auto;
  }

  &__buttons-container {
    margin-top: 50px;
    .button:first-of-type {
      margin-right: 10px;
    }
  }
}

.video-section {
  text-align: center;

  .button {
    margin-top: 30px;
  }
}

.about {
  &__content {
    display: flex;
    justify-content: space-between;
  }

  &__image-container {
    width: calc(66.6666% - 15px);
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    & .image-container {
      width: calc(50% - 10px);

      &:nth-of-type(n + 3) {
        margin-top: 20px;
      }

      & .image {
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        height: 250px;
      }
    }
  }

  &__text {
    &-container {
      width: calc(33.3333% - 15px);
    }

    &-paragraph {
      &:not(:last-of-type) {
        margin-bottom: 32px;
      }
    }
  }
}

.product {
  &__content {
    display: flex;
    justify-content: space-between;
  }

  &__content {
    & .image-container {
      width: calc(33.3333% - 30px);
    }
  }
}

.howItWorks {
  &__content {
    display: flex;
    justify-content: space-between;
  }
  &__item {
    width: calc(33.3333% - 30px);
    text-align: center;
    &-image {
      height: 100px;
      margin-bottom: 10px;
    }
    .heading-tertiary {
      margin-bottom: 20px;
    }
  }
}

.benefits {
  &__content {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  &__item {
    width: calc(50% - 30px);
    display: flex;

    &:nth-of-type(n + 3) {
      margin-top: 70px;
    }
    &-image {
      width: 70px;
      margin-right: 20px;
    }
    .heading-quarternary {
      margin-bottom: 10px;
    }
  }
}

.support {
  background: url(./assets/img/support-bg.jpg) center / cover no-repeat;

  .heading-tertiary {
    max-width: 50%;
    text-transform: uppercase;
  }
}

.footer {
  padding: 30px 0;

  &__links,
  &__social {
    display: flex;
    justify-content: center;
  }

  &__links {
    margin-bottom: 20px;
  }

  &__social {
    .contact-info__link-icon {
      font-size: 25px;
      color: #6e9941;
    }
  }
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 16;
  cursor: pointer;

  &--visible {
    opacity: 1;
    visibility: visible;
  }

  &--hidden {
    opacity: 0;
    visibility: hidden;
  }

  &__controls {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90%;
    display: flex;
    justify-content: space-between;
  }

  &__button {
    background-color: transparent;
    color: #fff;
    font-size: 40px;
    cursor: pointer;
    transition: color 0.15s ease;

    &:hover {
      color: #6e9941;
    }
  }

  &__item {
    position: relative;
    cursor: default;
    z-index: 2;

    & img {
      max-width: 800px;
      max-height: 80vh;
    }
  }
}

.image {
  $self: &;
  border-radius: 7px;
  width: 100%;
  box-shadow: 0px 4px 5px -2px rgba(56, 65, 73, 0.4);

  &-container {
    position: relative;
    cursor: pointer;

    &::after {
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      width: 100%;
      height: calc(100% - 6px);
      border-radius: 5px;
      background-color: rgba(56, 65, 73, 0.9);
      opacity: 0;
      transition: opacity 0.25s ease;
    }

    &:hover::after {
      opacity: 1;
    }

    &:hover #{$self}-open-button {
      opacity: 1;
    }
  }

  &-open-button {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 30px;
    color: #fff;
    z-index: 2;
    opacity: 0;
    transition: opacity 0.25s ease;
  }
}

.button {
  display: inline-block;
  font-weight: 500;
  font-size: 18px;
  border: 2px solid;
  cursor: pointer;
  transition: all 0.25s ease;

  &--sm {
    padding: 5px 15px;
  }

  &--lg {
    padding: 10px 45px;
  }

  &--square {
    border-radius: 5px;
  }

  &--round {
    border-radius: 500px;
  }

  &--white {
    background-color: #fff;
    color: #6e9941;
    border-color: #6e9941;

    &:hover {
      background-color: #6e9941;
      color: #fff;
    }
  }

  &--green {
    background-color: #6e9941;
    color: #fff;
    border-color: #6e9941;

    &:hover {
      background-color: #fff;
      color: #6e9941;
    }
  }

  &--gray {
    background-color: #fff;
    color: #222;
    border-color: #222;

    &:hover {
      background-color: #222;
      color: #fff;
    }
  }
}

.form {
  position: relative;
  padding: 50px;
  background-color: #fff;
  border-radius: 5px;
  text-align: center;

  &__close-button {
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(50%, -50%);
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background-color: #222;
    color: #fff;
    cursor: pointer;
  }

  .heading-tertiary {
    margin-bottom: 30px;
  }

  &__label {
    display: none;
  }

  &__field {
    &:not(:last-of-type) {
      margin-bottom: 30px;
    }
  }

  &__input,
  &__textarea {
    display: block;
    width: 100%;
    background-color: transparent;
    font-size: 16px;
    line-height: 20px;
    padding-bottom: 7px;
    border-bottom: 2px solid #929292;
    color: #222;
  }

  &__input {
    &:valid {
      border-color: #6e9941;
    }

    &:not(:valid) {
      border-color: #c63030;
    }

    &:placeholder-shown {
      border-color: #929292;
    }
  }

  &__textarea {
    max-width: 300px;
    min-width: 300px;
    max-height: 300px;
  }

  .button {
    margin-top: 30px;
  }
}
